* {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

body {
  margin: 8px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-heading {
  text-align: center;
  font-size: 25px;
}

.list-block {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.list-block__card {
  padding: 5px;
  border: 1px solid black;
  Width: 30%;
  margin-bottom: 10px;
}

.card__name {
  font-size: 20px;
  text-align: center;
}

.card__img {
  width: 100%;
}